.hidden{
    display: none;
}

.previewSitePage,
.previewViewPort,
.decorativeBackSplash,
.goldenBackSplash {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.previewViewPort {
    width: 90%;
    height: 60%;
    padding: 30px;
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
}

.decorativeBackSplash{
    background-color:rgba(255, 255, 255, 0.2);
    padding: 50px;
}
.previewViewPort ul {
    display: flex;
    font-size: 19px;
}
.previewViewPort li,
.previewViewPort h2,
.previewViewPort h3,
.previewViewPort h4 {
    margin: 10px;
}

.previewViewPort h2 {
    font-size: calc(20px + .2vw);
}

.previewViewPort h3 {
    font-size: calc(16.5px + .2vw );
}

.previewViewPort h4 {
    font-size: calc(15px + .2vw )
}

.highContrast{
    color: white;
    text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.24);
    background: #b9b9b9;
}

.golden {
    color: white;
    text-shadow: 1px 1px 2px rgb(232, 163, 0);
}
.goldenBackSplash {
    background-color: rgba(223, 172, 17, 0.59);
    padding: 50px;
}

.mermaid{
    color: white;
    text-shadow: 1px 1px .5px rgb(0, 108, 58);
}

.light{
    color: grey;
    text-shadow: 1px 1px 0px rgb(44, 44, 44);
} 


